@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  height: 100%;
  width: 100%;
  font-family: 'Montserrat';
}

#root {
  height: 100%;
  width: 100%;
}
.App-Header {
  background-color: #13e796;
  padding: 0 10px;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
}

.App-Title {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    color: #131f22;
}

.App-Title span {
    margin-left: 5px;
    color: #131f22;
}

.App-Nav {
    width: 100%;
    height: 100%;
}

.App-Nav ul {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.App-Nav ul li {
    width: 100px;
    height: 60%;
    margin: 0 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.App-Nav ul li a {
    width: 100%;
    height: 100%;
    text-decoration: none;

    background-color: #131f22;
    color: #fff;
    border-radius: 4px;

    display: flex;
    align-items: center;
    justify-content: space-evenly;

    transition: 0.25s ease-in-out;
}

.App-Nav ul li a:hover {
    border-radius: 0px;
    color: #13e796;
}

.App-Nav ul li a.active {
    color: #13e796;
}
.App-Separator {
    width: 90%;
    height: 2px;
    background-color: #13e796;
    margin: 30px 0;
    border-radius: 100px;
}
.Home-Intro {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Home-Intro h2 {
    width: 80%;
    font-size: 30px;
    margin-bottom: 10px;

    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid #fff;
}

.Home-Intro .Intro-Content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Home-Intro .Intro-Content .Text-Intro {
    width: 80%;
    text-align: center;
    margin: 20px 0;
}
.Home-UsefulLinks {
  width: 90%;
  padding: 6px;

  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff15;
}

.Home-UsefulLinks h2 {
  width: 80%;
  font-size: 30px;
  margin-bottom: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #fff;
}

.Home-UsefulLinks .UsefulLinks-Content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.Home-UsefulLinks .UsefulLinks-Content a {
  height: 150px;
  width: 200px;
  padding: 5px;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
}

.Home-UsefulLinks .UsefulLinks-Content a img {
  max-height: 100%;
  max-width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.Home-UsefulLinks .UsefulLinks-Content a:hover::after {
  content: attr(data-name);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  background-color: rgba(29, 35, 39, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #13e796;
  font-size: 20px;
  font-weight: 600;
}

.Card-Content .Image-Container {
  height: 150px;
  width: 150px;
  padding: 8px;
}

.Card-Content .Image-Container img {
  height: 100%;
  width: 100%;
  border: 3px solid #fff;
  border-radius: 8px;
  cursor: pointer;
}

.Card-Popup {
  height: 110px;
  width: 250px;
  padding: 8px;

  background-color: #fff;
  border-radius: 8px;

  display: flex;
  flex-direction: column;
  align-items: center;

  box-shadow: 3px 3px 4px #00000085;
}

.Card-Popup-Social {
  padding: 8px;
}

.Card-Popup-Social a {
  outline: none;
}

.Home-Gallery {
  width: 90%;
  padding: 6px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Home-Gallery h2 {
  width: 80%;
  font-size: 30px;
  margin-bottom: 20px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #fff;
}

.Gallery-Content {
  width: 100%;
  display: grid;
  grid-template-columns: 25% 75%;
}

.Gallery-Content .Gallery-Teacher {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Gallery-Content .Gallery-Teacher div {
  padding: 8px;
}

.Gallery-Content .Gallery-Teacher div img {
  border: 3px solid #fff;
  border-radius: 8px;
}

.Gallery-Tutors {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  justify-content: center;
  align-items: center;
  justify-items: center;
  align-content: center;
}

.Home-Content {
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.Archive-List {
  background-color: #4e525060;
  padding: 8px;
  margin-bottom: 30px;
  border-radius: 4px;
}

.Archive-Title {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #fff;
  padding: 5px;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.ArchiveOL {
  list-style: none;
  padding: 10px;
}

.ArchiveOL .ArchiveOL-Item {
  padding: 5px;
  margin: 4px 0;
}

.ArchiveOL li .semester {
  width: 100%;
  border-bottom: 2px solid #fff;
  cursor: pointer;
  transition: 0.25 ease-in-out;
  font-weight: 600;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ArchiveOL li .semester.selected {
  border-bottom: 2px solid #13e796;
}

.ArchiveOL li .semester:hover {
  border-bottom: 2px solid #13e796;
}

.ArchiveOL-Content {
  padding: 8px 3px;
  list-style: none;
  display: flex;
  align-items: center;
}

.ArchiveOL-Content li {
  margin: 0 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ArchiveOL-Content li a {
  width: 100%;
  height: 100%;
  color: #13e796;
  text-decoration: none;
  transition: 0.25s ease-in-out;
}

.ArchiveOL-Content li a:hover {
  text-decoration: underline;
}

.App {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 5% 93% 2%;
  background-color: #131f22;
}

.App-content {
  padding: 50px 8px 8px 8px;
  /* background-color: #131f22; */
  color: #fff;
  overflow: auto;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.App .App-footer {
  font-size: 12px;
  height: 100%;
  color: #fff;
  text-align: center;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

